.landing-page {
  min-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color:  #FFC107;
  // background-image: url(asset/image/background-image.png);
}
.landing-page__item {
  background-color: #FFC107;
}

.landing-page__wrapper {
  text-align: center;
}

.landing-page__name {
  font-size: 7rem;
  text-transform: uppercase;
  color: #E64A19;
  font-family: 'Permanent Marker', cursive;
  
}

.landing-page__title {
  font-size: 1.8rem;
  color: #212121;
  font-family: Mali;
}

